.listItemLi {
  display: flex;
  height: 100px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  justify-content: left;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0px;
  border-bottom: 0.5px solid grey;
}
.listItemImg {
  background-color: rgb(139, 138, 138);
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.listItemName {
  display: flex;
  height: 80px;
  align-items: up;
  justify-content: space-between;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  padding-left: 10px;
  overflow: hidden;
  width: 500px;
}
.listItemText {
  display: flex;
  justify-content: left;
  margin: 0;
  text-align: left;
  text-overflow: ellipsis;
  overflow: auto;
  width: 100px;
}
.listItemTextBottom {
  font-size: small;
  width: 100px;
  height: 20px;
  margin: 0;
  overflow: hidden;
}

.listItemLeft {
  padding-top: 13px;
  justify-content: center;
  align-items: center;
  margin: 0;
}
.listItemBottom {
  display: flex;
  margin: 0;
  height: 24px;
  font-size: small;
  padding-top: 13px;
  padding-right: 10px;
  /* width: 150px; */
  text-overflow: ellipsis;
  overflow: hidden;
}
